<template>
  <v-form @change.native="onChange">
    <v-container>
      <v-row no-gutters>
        <v-col cols="6">
          <v-textarea name="input" label="粘贴" @paste="onPaste"></v-textarea>
        </v-col>
        <v-col cols="6">
          <v-textarea v-model="output" name="output" label="输出" ref="output"></v-textarea>
        </v-col>
        <template v-for="key in keys">
          <v-col v-if="key[0]=='-'" :key="key" cols="12">
            <h3 class="title primary--text" color="primary">{{labels[key]}}</h3>
          </v-col>
          <v-col v-else-if="textareas[key]" :key="key" cols="12" sm="6" md="3">
            <v-textarea :label="labels[key]" v-model="values[key]" :hint="key"></v-textarea>
          </v-col>
          <v-col v-else :key="key" cols="12" sm="6" md="3">
            <v-text-field :label="labels[key]" v-model="values[key]" :hint="key"></v-text-field>
          </v-col>
        </template>
      </v-row>
    </v-container>
    <v-btn @click="onDone" color="primary" dark fixed bottom right fab>
      <v-icon>done</v-icon>
    </v-btn>
  </v-form>
</template>

<script>
import {
  textareas,
  labels,
  keys,
  fields,
  httpUserAgent
} from "../data/const.js";
function getDefaultValues() {
  const values = {};
  fields.forEach(a => (values[a] = ""));
  values.httpUserAgent = httpUserAgent;
  return values;
}
export default {
  name: "Editor",
  data() {
    var v = localStorage.getItem("book.source.editor");
    const values = getDefaultValues();
    if (v) {
      Object.assign(values, JSON.parse(v));
    }
    values.httpUserAgent = httpUserAgent;
    return {
      keys,
      textareas,
      labels,
      values,
      output: ""
    };
  },
  methods: {
    onPaste(e) {
      let json;
      try {
        json = JSON.parse(e.clipboardData.getData("text/plain"));
      } catch (error) {}
      if (json) {
        delete json["httpUserAgent"];
        Object.assign(this.values, getDefaultValues(), json);
      }
    },
    onDone() {
      const result = {};
      const values = Object.assign({}, this.values);
      fields.forEach(a => (result[a] = values[a]));
      result.httpUserAgent = httpUserAgent;
      this.output = JSON.stringify(result, null, 2);
      this.$nextTick(() => {
        this.$refs.output.$refs.input.select();
        document.execCommand("copy");
        this.reset({ output: this.output });
      });
    },
    reset(data) {
      localStorage.removeItem("book.source.editor");
      Object.assign(this.$data, this.$options.data(), data);
    },
    onChange() {
      localStorage.setItem(
        "book.source.editor",
        JSON.stringify(Object.assign({}, this.values))
      );
    }
  }
};
</script>
<style lang="scss" scoped>
.no-gutters > .col,
.no-gutters > [class*="col-"] {
  padding: 0 1em;
}
</style>
